#canvasDiv {
    border: 1px solid #000;
}

.relative {
position: relative;
}

.flex-center-col {
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.flex-start-row {
    display: flex;
    justify-content: flex-start;
    flex-direction: row;
}


.nonActiveToolBtn {
    background-color: rgba(0, 0, 0, 0);
    border: 1px solid rgba(0, 0, 0, 0);
    border-radius: 5px;
    padding: 9px;
}
.nonActiveToolBtn:hover {
    background-color: rgba(0, 0, 0, 0.1);
    border: 1px solid rgba(0, 0, 0, 0);
    border-radius: 5px;
    padding: 9px;
}

.activeToolBtn {
    background-color: rgba(20, 0, 0, 0.2);
    color: black;
    border: 1px solid rgba(0, 0, 0, 0.3);
    border-radius: 5px;
    padding: 9px;
}

.toolbar {
    margin: 5px;
    position: relative;
}

.canvasTitleBar{
    background-color: var(--black-color);
    color: var(--white-color);
    padding: 10px;
    margin-bottom: 5px;
}

.brushSizeDiv {
    position: absolute;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: row;
    top: 145px;
    left: 50px;
    z-index: 2;
    background-color: var(--white-color);
    border-radius: 10px;
    border: 1px solid var(--gray-color-02);
    padding: 4px;
    box-shadow: 2px 2px 5px #aaaaaa;
  }

.closebrushSizeBtn{
    color: var(--black-color);
    background-color: rgba(255, 255, 255, 0);
    border: none;
}

.closebrushSizeBtn:hover {
    cursor: pointer;
    color: var(--gray-color);
}


/* 컬러 프리뷰 구역 */

.PreviewColorSquare{
    width: 30px;
    height: 30px;
    border: 1px solid #555555;
}


.PreviewColorRectangle{
    width:100px;
    height: 30px;
    margin-right: 10px;
    border: 1px solid #bbbbbb;
    border-radius: 5px;
    display : flex;
    justify-content : center;
    align-items : center;
}


.mainColorDiv {
    position: relative;
    z-index: 3;
}

.subColorDiv {
    position: relative;
    bottom: 20px;
    left: 10px;
    z-index: 1;
}

.PreviewColors{
    position: relative;
    height: 45px;
    width : 40px;
}

.changeMainSubColorBtn{
    position:absolute;
    top: -4px;
    z-index:3;
    left: 32px;
    color : var(--gray-color);
}

.changeMainSubColorBtn:hover{
    color: var(--main-color);
    cursor: pointer;
}

.changeMainSubColorIcon{
    width: 13px;
    height: 13px;
}



/* undo, redo */
.historyControlDiv{
    margin: 15px;
    position: absolute; 
    left: 50px; 
    top: -2px;
    z-index: 2;
    display: flex;
    gap: 10px;
}

.historyControlIcon{
    width: 20px;
    height: 20px;
    color: var(--gray-color);
    text-shadow: -1px 0px yellow, 0px 1px yellow, 1px 0px yellow, 0px -1px yellow;
}

.historyControlIcon:hover{
    color: var(--main-color);
    cursor: pointer;
}
    