:root{
    --black-color: #242631;
  --gray-color: #999ba7;
  --gray-color-02: #797c8a;
  --gray-color-03: #cccdd4;
  --light-gray-color: #e3e3e8;
  --light-gray-color-02: #f1f1f3;
  --white-color: #fff;
  --main-color: #7457e9;
  --main-bg-color: hsl(252, 77%, 97%);
  --main-active-color: rgba(117, 88, 233, 0.8);
  --red-color: #f51d46;
  --red-active-color: rgba(245, 29, 69, 0.8);
}


@font-face {
    font-family: 'Pretendard-Regular';
    src: url('https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
    font-weight: 400;
    font-style: normal;
}

@font-face {
    font-family: 'BookkMyungjo-Bd';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2302@1.0/BookkMyungjo-Bd.woff2') format('woff2');
    font-weight: 700;
    font-style: normal;
}

*{
    font-family: 'Pretendard-Regular';
}

.paletteTitleFont{
    font-size: 18px;
    font-weight: 600;
    color: var(--black-color);
}

.gray {
    color: var(--gray-color-02);
}

.center-align-row {
    display: flex;
    justify-content: center;
    flex-direction: row;
}

.colorModal {
    background-color: var(--white-color);
    border: 1px solid var(--gray-color-03);
    margin: 10px;
    border-radius: 5px;
    box-shadow: 2px 2px 20px #aaaaaa;
    position: absolute; 
    right: 0px; 
    bottom: 0px;
}


.btn_close_color {
    background-color: var(--main-color);
    box-shadow: 1px 1px 3px #555555;
}

.btn_open_color{
    background-color: var(--main-active-color);
    box-shadow: inset 1px 1px 3px #555555;
}


.openbtn{
    margin: 5px;
    border: none;
    border-radius: 5px;
    padding: 9px;
    color:white;
}
.openbtn:hover{
    cursor: pointer;
    background-color: var(--main-active-color);
    color: var(--white-color);
}

.colorPalette {
    display: flex;
    width: 255px;
    margin: 10px;
    justify-content: flex-start;
    flex-direction: row;
    flex-wrap: wrap;
    gap:5px;
}

.colorItem {
    
    width: 30px;
    height: 30px;
    border-radius: 50%;
    border:1px solid var(--gray-color-03);

}
.colorItem:hover {
    border: 1px solid var(--gray-color-02);
}

.colorAddItem {
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--gray-color);
    
}
.colorAddItem:hover {
    color: var(--gray-color-02);
    background-color: var(--light-gray-color);
    border: 1px solid var(--gray-color-02);
}

.menu {
    position: fixed;
    background-color: white;
    border: 1px solid #888888;
    font-size: small;
    border-radius: 2px;
    padding: 4px;
    
}
.menu:hover {
    cursor: pointer;
    background-color: #dddddd;
}


.paletteTitlebox {
    display: flex;
    margin: 0px 5px 8px 5px;
    align-items: center;
    flex-direction: row;
    gap:5px;
    /* background-color: var(--light-gray-color-02); */
    padding: 3px 5px;
    border-radius: 5px;
}

.eyeDropperBtn {
    color: var(--main-color);
    background-color: rgba(255, 255, 255, 0);
    border:1px solid rgba(255, 255, 255, 0);
    border-radius: 5px;
    padding: 5px;
}
.eyeDropperBtn:hover{
    cursor: pointer;
    color:  var(--main-active-color);
    border: 1px solid var(--main-active-color);
}

.colorModalHeader{
    display: flex;
    justify-content: space-between;
    background-color: var(--black-color);
    padding: 10px;
    border-radius: 5px 5px 0px 0px;
    color: white;
}

.colorModalContent{
    padding: 10px;
}

.ColorElementInput {
    width: 25px;
    padding: 5px;
    border: 1px solid #aaaaaa;
    border-radius: 5px;
    margin-right: 3px;
}
.ColorElementInput:focus{
    outline: solid 1px var(--main-active-color);
}


.colorHexInput {
    width: 100px;
}

.colorElementBox {
    margin: 10px;
}


.closeModalBtn{
    color: #f1f1f3;
    background-color: rgba(255, 255, 255, 0);
    border: none;
}
.closeModalBtn:hover {
    cursor: pointer;
    color: var(--gray-color);
}


.resetIcon{
    width: 20px;
    height: 20px;
}

.resetBtn{
    display: flex;
    justify-content: center;
    align-items: center;
    /* border: 1px solid var(--main-color); */
    border: 1px solid rgba(255, 255, 255, 0);
    border-radius: 5px;
    color: var(--main-color);
    background-color: rgba(255, 255, 255, 0);
    padding: 1px;
}
.resetBtn:hover {
    cursor: pointer;
    color: var(--main-active-color);
    border: 1px solid var(--main-active-color);
}

.explain{
    font-size: 12px;
    font-style: italic;
    color: rgba(255, 255, 255, 0);
}
.resetBtn:hover+.explain {
    color: var(--main-active-color);
}



/* 컬러피커 */

.colorPickerModal {
    background-color: var(--white-color);
    border: 1px solid var(--gray-color-03);
    margin: 10px;
    border-radius: 5px;
    box-shadow: 2px 2px 20px #aaaaaa;
    position: absolute; 
    left: 50px; 
    top: 40px;
    z-index: 5;
}

.colorPicker{
    margin-top: 10px;
}